import { Box, Button, Container, Grow, IconButton, Toolbar, Typography } from "@mui/material";
import Header from "../components/Header";
import RegistrationInfo from "../components/RegistrationInfo";
import { ArrowBack, NavigateNext } from "@mui/icons-material";
import { useLocation, useNavigate } from "react-router-dom";
import RegistrationStepper from "../components/RegistrationStepper";
import { ChangeEvent, useState } from "react";
import { PlanInfo, RegistrationForm } from "../services/types";
import { FormControl, RadioGroup } from "@mui/joy";
import PlanPanel from "../components/PlanPanel";

const plans: PlanInfo[] = [
    {
        planId: "plan1",
        planName: "",
        price: 0,
        priceLabel: "",
        priceDescription: "2",
        planDescription: "",
    },
    {
        planId: "plan2",
        planName: "",
        price: 27800,
        priceLabel: "27,800/",
        priceDescription: "27,800",
        planDescription: "\n2",
    },
    {
        planId: "plan3",
        planName: "",
        price: 37600,
        priceLabel: "37,600/",
        priceDescription: "37,600",
        planDescription: "\n",
    },
]

function SelectPlan() {

    const navigate = useNavigate();
    const location = useLocation();

    const [form, setForm] = useState<RegistrationForm>(location.state ?? {
        businessForm: "",
        familyName: "",
        firstName: "",
        familyNameKana: "",
        firstNameKana: "",
        planId: "",
        planName: "",
        planPrice: 0,
        planPriceLabel: "",
    });

    const handleChange = (event: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        // 
        const { value } = event.target;
        const selectedPlan = plans.find((plan) => plan.planId === value);
        setForm({...form,
            "planId": value,
            "planName": selectedPlan?.planName ?? "",
            "planPrice": selectedPlan?.price ?? 0,
            "planPriceLabel": selectedPlan?.priceLabel ?? ""
        });
    }

    const [checked, setChecked] = useState(true);

    // useEffect(() => {
    //     location.state && setForm(location.state);
    // }, [])

    return (
        <Box sx={{ display: 'flex', backgroundColor: 'linen', minHeight: '100vh' }} >
            <Header />

            <Container maxWidth="md">
                <Toolbar />
                <Box sx={{ my: 8, p: 4, borderRadius: 2, backgroundColor: 'white' }}>

                    {/* Stepper */}
                    <Box sx={{ display: "flex" }}>
                        <Box sx={{ width: 60 }}>
                            <IconButton
                                sx={{ border: 1 }}
                                onClick={() => navigate('/enter-account-info', { state: form })}
                            >
                                <ArrowBack />
                            </IconButton>
                        </Box>
                        <Box sx={{ flexGrow: 1 }}>
                            <RegistrationStepper activeStep={2} />
                        </Box>
                        <Box sx={{ width: 60 }} />
                    </Box>

                    {/*  */}
                    <Typography variant="h5" sx={{ mt:8, textAlign: "center" }}>
                        <b></b>
                    </Typography>

                    {/*  */}
                    <Box sx={{ mt: 6, mx: 3 }}>
                        <FormControl>
                            <RadioGroup
                                orientation="horizontal"
                                name="planId"
                                value={form.planId}
                                onChange={(e) => handleChange(e)}
                                sx={{ gap: 2 }}
                            >
                                {plans.map((plan) => (
                                    <PlanPanel plan={plan} />
                                ))}
                            </RadioGroup>
                        </FormControl>
                    </Box>

                    {/*  */}
                    <Box sx={{ mt: 4, mx: 12, display: "flex", justifyContent: "center" }}>
                        <Button
                            size="large"
                            variant="contained"
                            onClick={() => navigate("/registration-confirmation", { state: form })}
                            endIcon={<NavigateNext />}
                            sx={{ width: 240}}
                        >
                            
                        </Button>
                    </Box>
                </Box>
            </Container>

            <RegistrationInfo selectingPlan={{
                "productName": "",
                "planName": form.planName,
                "priceLabel": form.planPriceLabel,
            }} />
        </Box>
    );
}

export default SelectPlan;